import Vue from 'vue';
import Dayjs from 'dayjs';
import 'dayjs/locale/zh-cn';
import relativeTime from 'dayjs/plugin/relativeTime';
// 配置中文语言包
Dayjs.locale('zh-cn');

// 配置使用处理相对时间
Dayjs.extend(relativeTime);

// 处理相对时间
Vue.filter('formatTime', (value) => Dayjs().to(Dayjs(value)));

Vue.filter('commentDate', (value, format = 'YYYY-MM-DD HH:mm:ss') => Dayjs(value).format(format));
