import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {

    path: '/',
    name: 'Layout',
    component: () => import('@/views/layout/'),
    meta: {
      requireAuth: false, // 是否需要登录
    },

    children: [
      {
        path: '',
        name: 'Home',
        component: () => import('@/views/home/'),
        meta: {
          requireAuth: false,
        },
      },
      {
        path: '/telphone',
        name: 'Telphone',
        component: () => import('@/views/telphone/'),
        meta: {
          requireAuth: false,
        },
      },
      {
        path: '/list',
        name: 'List',
        component: () => import('@/views/list/'),
        meta: {
          requireAuth: false,
        },
      },
      {
        path: '/myInfo',
        name: 'Myinfo',
        component: () => import('@/views/myInfo/'),
        meta: {
          requireAuth: false,
        },
      },

    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login/'),
    meta: {
      requireAuth: false,
    },
  },

  {
    path: '/search',
    name: 'SearchPage',
    component: () => import('@/views/search/'),
    meta: {
      requireAuth: false,
    },
  },
  {
    path: '/article/:articleId',
    name: 'ArticlePage',
    component: () => import('@/views/article/'),
    props: true, // 将路由中动态参数映射到组件props中

    meta: {
      requireAuth: false,
    },
  },
  {
    path: '/user/profile',
    name: 'UserProfile',
    component: () => import('@/views/userProfile/'),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: '/user/chat',
    name: 'UserChat',
    component: () => import('@/views/userChat/'),
    meta: {
      requireAuth: true,
    },
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes,
});

export default router;
