import Vue from 'vue';
import Vuex from 'vuex';
import { getItem, setItem, removeItem } from '@/utils/storage';

const USER_KEY = 'userToken';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    socket: null,
    userToken: getItem(USER_KEY), // 获取本地存储的token
    includeList: ['Layout', 'Home', 'Myinfo'], // 不需要缓存的组件
  },

  mutations: {
    // 存储token数据
    setToken(state, payload) {
      state.userToken = payload;
      setItem(USER_KEY, payload);
    },
    removeCachePage(state, name) {
      const index = state.includeList.indexOf(name);
      if (index !== -1) {
        state.includeList.splice(index, 1);
      }
    },
    addCachePage(state, name) {
      if (!state.includeList.includes(name)) {
        state.includeList.push(name);
      }
    },

  },
  actions: {
  },
  modules: {
  },
});
